angular.module('well.admin', [])
  .controller('AdminCtrl', function ($scope, User) {
    User.isAdmin().then((isAdmin) => {
      $scope.isAdmin = isAdmin
    })

    $scope.data = {
      type: 'JSON'
    }
    
    User.getAllData('threetwo').then((data) => {
      $scope.data.threetwo = data
    })

    User.getAllData('overlap').then((data) => {
      $scope.data.overlap = data
    })

    User.getAllData('fineoverlap').then((data) => {
      $scope.data.fineoverlap = data
    })
  })
