/* Directives */

angular.module('well.directives', [])
  .directive('earth', function ($window, Aspects) {
    return {
      restrict: 'EA',
      scope: {},
      link: function (scope, element, attrs) {
        window.onresize = function() {
          scope.$apply()
        }

        scope.$watch(function() {
          return angular.element($window)[0].innerWidth
        }, function() {
          scope.render()
        })

        var splash = d3.select('.splash')

        scope.render = function () {
          splash.selectAll('.earth *').remove()

          var width = window.innerWidth,
              height = window.innerHeight

          var projection = d3.geo.orthographic()
              .precision(0)
              .scale(500)
              .translate([width / 2, height / 2])
              .clipAngle(180)

          var canvas = d3.select('.splash .earth').append('canvas')
              .attr('width', width)
              .attr('height', height)

          var context = canvas.node().getContext('2d')

          var path = d3.geo.path()
              .projection(projection)
              .context(context)

          var delta = 0
          var offset = 0
          var ln = d3.random.logNormal(1)
          _.each(scope.responses, function (resp) {
            resp.rand = Math.random() * 20 + 10
          })

          d3.json('world-110m.json', function(error, world) {
            var x, y = 0
            window.onscroll = function () {
              y = (window.pageYOffset || document.scrollTop)  - (document.clientTop || 0) || 0
              rotate()
            }

            d3.select('body').on('mousemove', function () {
              x = d3.mouse(this)[0]
              rotate()
            })

            var land = topojson.feature(world, world.objects.land)
            var countries = topojson.feature(world, world.objects.countries).features

            _.each(countries, function (country) {
              country.properties.color = Math.random()
            })
            rotate()

            function rotate () {
              projection.rotate([x * .1, y * .1, 30])
              context.clearRect(0, 0, width, height)

              _.each(countries, function (country) {
                context.beginPath()
                context.strokeStyle = d3.hsl(country.properties.color * x, 1, .8).toString()
                path(country)
                context.stroke()
              })
            }
          })
        }
      }
    }
  })
  .directive('range', function($window, $timeout, Aspects) {
    return {
      restrict: 'EA',
      scope: {
        level: '='
      },
      link: function(scope, element, attrs) {
        var svg = d3.select(element[0])
          .append('svg')
          .attr('width', '100%')
          .attr('height', 22)

        window.onresize = function() {
          scope.$apply()
        }

        scope.$watch(function() {
          return angular.element($window)[0].innerWidth
        }, function() {
          scope.render()
        })

        var lastLevel = null
        scope.$watch('level', function (level) {
          if (level !== lastLevel) {
            scope.render()
          }
        })

        scope.render = function () {
          svg.selectAll('*').remove()

          var width = d3.select(element[0])[0][0].offsetWidth
          var height = 22

          var x = d3.scale.linear()
              .domain([0, 100])
              .range([6, width - 6])
              .clamp(true)

          var brush = d3.svg.brush()
              .x(x)
              .extent([scope.level, scope.level])
              .on('brush', brushed)

          var axis = svg.append('g')
              .attr('class', 'axis')

          axis.call(d3.svg.axis()
              .scale(x)
              .orient('bottom')
              .tickSize(0)
              .tickValues([]))
          
          var domain = axis.select('.domain')
              .attr('transform', 'translate(0, 10)')

          domain.select(function() { return this.parentNode.appendChild(this.cloneNode(true)); })
              .attr('class', 'halo')

          var slider = svg.append('g')
              .attr('class', 'slider')
              .call(brush)

          slider.selectAll('.extent,.resize')
              .remove()

          slider.select('.background')
              .attr('height', height)

          var pointy = function () {
            var cx = x(1),
              cy = 10
            return {
              set: function (pt) {
                cx = pt
                return [
                  { x: cx - 6, y: cy - 6 },
                  { x: cx + 6, y: cy - 6 },
                  { x: cx + 6, y: cy + 6 },
                  { x: cx, y: cy + 10 },
                  { x: cx - 6, y: cy + 6 }
                ]
              }

            }
          }

          var line = d3.svg.line()
            .x(function (d) { return d.x })
            .y(function (d) { return d.y })

          var handle = slider.append('path')
              .attr('class', 'handle')
              .attr('d', line(pointy().set(x(scope.level))) + 'Z')
              .style('display', 'none')

          // slider
          //     .call(brush.event)

          function brushed() {
            var value = brush.extent()[0]

            var v = x.invert(d3.mouse(this)[0])
            value = isNaN(v) ? value : v
            brush.extent([value, value])

            if (value) value = Math.round(value)

            handle
              .style('display', 'block')
              .attr('d', line( pointy().set( x(value) ) ) + 'Z')
            
            lastLevel = value
            $timeout(function () {
              scope.level = value
            })
          }
        }
      }
    }
  })
  .directive('bar', function($window, Aspects, $filter) {
    return {
      restrict: 'E',
      scope: {
        level: '=',
        name: '=',
        resize: '='
      },
      link: function(scope, element, attrs) {
        var svg = d3.select(element[0])
          .append('svg')
          .attr('width', '100%')
          .attr('height', 50)

        var g = svg.append('g')

        window.onresize = function() {
          scope.$apply()
        }

        scope.$watch('level', function () {
          scope.render()
        })

        scope.$watch('name', function () {
          scope.render()
        })

        scope.$watch('resize', function () {
          console.log('resize', scope.resize)
          scope.render()
        })

        scope.$watch(function() {
          return angular.element($window)[0].innerWidth
        }, function() {
          scope.render()
        })

        scope.render = function () {
          g.selectAll('*').remove()

          var w = d3.select(element[0])[0][0].offsetWidth
          var width = w > 0 ? w : 100
          var height = 40

          var x = d3.scale.linear()
              .domain([0, 100])
              .range([6, width - 6])

          var axis = g.append('g')
              .attr('class', 'axis')
              .attr('transform', 'translate(0,' + (height - 20) + ')')

          axis.call(d3.svg.axis()
              .scale(x)
              .orient('bottom')
              .tickSize(0)
              .tickPadding(12))

          axis.select('.domain').remove()

          var backBar = g.append('rect')
              .attr('class', 'backbar')
              .attr('x', x(0))
              .attr('y', 4)
              .attr('height', 20)
              .attr('width', x(100) - x(0))

          var barBack = g.append('rect')
              .attr('class', 'barBack')
              .attr('x', x(0))
              .attr('y', 4)
              .attr('height', 20)
              .attr('width', x(100) - x(0))
              .style('fill', '#eee')

          var bar = g.append('rect')
              .attr('class', 'bar')
              .attr('x', x(0))
              .attr('y', 4)
              .attr('height', 20)
              .attr('width', x(scope.level) - x(0))
              .style('fill', Aspects.getColor(scope.name, 100))

          var text = g.append('text')
              .attr('class', 'text')
              .attr('y', 14)
              .attr('dy', '.375em')
              .style('fill', '#000')
              .style('font-weight', '400')

          var xPos = scope.diff ? x(scope.level + scope.diff) : x(scope.level)
          
          if (scope.level) {
            g.append('line')
                .attr('x1', xPos)
                .attr('y1', 4)
                .attr('x2', xPos)
                .attr('y2', 24)
                .style('stroke', '#000')
          }
        }
      }
    }
  })
  .directive('diff', function($window, Aspects, $filter, $timeout, Survey) {
    return {
      restrict: 'E',
      scope: {
        level: '=',
        diff: '=',
        name: '=',
        choice: '=',
        animate: '=',
        delta: '=',
        option: '@'
      },
      link: function(scope, element, attrs) {
        var width = element[0].parentNode.getBoundingClientRect().width
        var svg = d3.select(element[0])
          .append('svg')
          .attr('width', width)
          .attr('height', 180)

        var g = svg.append('g').attr('transform', 'translate(0,30)')
        var zoom

        window.addEventListener('resize', function() {
          scope.render(true)
        })

        scope.$watch('level', function () {
          scope.render()
        })

        scope.$watch('diff', function (diff) {
          scope.render()
        })

        scope.$watch('name', function () {
          scope.render()
        })

        var diffDur = 2000
        var magDur = 1000

        // scope.$watch('choice', function () {
        //   if (attrs.option === 'one' && scope.name) {
        //     scope.animate = 'one'
        //     $timeout(() => { scope.animate = 'two' }, diffDur + magDur)
        //   }
        //   scope.render()
        // }, true)

        scope.$watch('animate', function () {
          console.log('animate', scope.animate)
          if (scope.name && scope.animate) {
            scope.render(true)
          }
        })

        var d = 0
        var diffVal = 0

        var range = [0, 100]
        // scope.init = (animate) => {
        //   if (range) {
        //     scope.render(animate)
        //   } else {
        //     Survey.range().then(r => {
        //       range = r
        //       scope.render(animate)
        //     })
        //   }
        // }

        var scale = d3.select(element[0]).append('div').attr('class', 'scale')
        scale.append('span').text('0').attr('class', 'left')
        scale.append('span').text('100').attr('class', 'right')


        scope.render = function (animate) {
          width = element[0].parentNode.getBoundingClientRect().width
          svg.attr('width', width)
          // axis.attr('width', width)
          g.selectAll('*').remove()
          if (zoom) zoom.remove()

          width -= 30
          var height = 50
          var top = 42

          var x = d3.scale.linear()
              .domain(range)
              .range([21, width - 6])

          // axis.call(d3.svg.axis()
          //     .scale(x)
          //     .orient('bottom')
          //     .tickSize(0)
          //     .tickPadding(12))

          // axis.select('.domain').remove()

          if(x(range[1]) - x(range[0]) > 0) {
            var backBar = g.append('rect')
                .attr('class', 'backbar')
                .attr('x', x(range[0]))
                .attr('y', top + 4)
                .attr('height', 20)
                .attr('width', x(range[1]) - x(range[0]))

            var barBack = g.append('rect')
                .attr('class', 'barBack')
                .attr('x', x(range[0]))
                .attr('y', top + 4)
                .attr('height', 20)
                .attr('width', x(range[1]) - x(range[0]))
                .style('fill', '#eee')

            var bar = g.append('rect')
                .attr('class', 'bar')
                .attr('x', x(range[0]))
                .attr('y', top + 4)
                .attr('height', 20)
                .attr('width', x(scope.level) - x(range[0]))
                .style('fill', Aspects.getColor(scope.name, 100))
          }

          var diff = g.append('rect')
              .attr('class', 'diff')
              .attr('y', top + 4)
              .attr('height', 20)
              .style('fill', Aspects.getColor(scope.name, 205))

          var rightArrow = (start, end, y) => {
            y += top
            return 'M' + x(start) + ' ' + Number(y + 2) + ' L' + Number(x(end) - 4) + ' ' + Number(y + 2) +
              ' L' + Number(x(end) - 4) + ' ' + y + ' L' + x(end) + ' ' + Number(y + 3) + ' L' + Number(x(end) - 4) + ' ' + Number(y + 6) +
              ' L' + Number(x(end) - 4) + ' ' + Number(y + 4) + ' L' + x(start) + ' ' + Number(y + 4) + ' Z'
          }

          var leftArrow = (start, end, y) => {
            y += top
            return 'M' + x(start) + ' ' + Number(y + 2) + ' L' + Number(x(end) + 4) + ' ' + Number(y + 2) +
              'L' + Number(x(end) + 4) + ' ' + y + ' L' + x(end) + ' ' + Number(y + 3) + ' L' + Number(x(end) + 4) + ' ' + Number(y + 6) +
              'L' + Number(x(end) + 4) + ' ' + Number(y + 4) + ' L' + x(start) + ' ' + Number(y + 4) + ' Z'
          }

          var arrow = g.append('path')
              .attr('class', 'arrow')
              .attr('stroke', 'none')
              .style('fill', '#000')

          var text = g.append('text')
              .attr('class', 'text')
              .attr('y', top + 14)
              .attr('dy', '.375em')
              .style('fill', '#000')
              .style('font-weight', '400')  

          var xPos = scope.diff ? x(scope.level + scope.diff) : x(scope.level)
          if (scope.level) {
            g.append('line')
              .attr('x1', xPos)
              .attr('y1', top + 4)
              .attr('x2', xPos)
              .attr('y2', top + 24)
              .style('stroke', '#000')
          }

          if (animate) {

            if (scope.diff) {
              var eight = x(12) - x(0)
              zoom = svg.select(function() { return this.appendChild(this.cloneNode(true)) })
                .attr('class','zoom')

              var id = 'clip' + Math.random().toString().replace('.','')
              var clip = zoom.append('defs').append('clipPath').attr('id', id)
              clip.append('circle')
                .attr('cx', xPos)
                .attr('cy', top * 2)
                .attr('r', eight)

              zoom.insert('circle', 'g')
                .attr('cx', xPos)
                .attr('cy', top * 2)
                .attr('r', eight)
                .style('fill', '#fff')

              zoom.append('circle')
                .attr('cx', xPos)
                .attr('cy', top * 2)
                .attr('r', eight)
                .style('fill', 'none')
                .style('stroke','#000')
                .style('stroke-width','2px')

              var bounds = svg[0][0].getBoundingClientRect()
              zoom
                .attr('viewBox', xPos / 2 + ' ' + Number(top / 2 + 20) + ' ' + bounds.width / 2 + ' ' + bounds.height / 2)
                .attr('clip-path', 'url(#' + id +')')
                .transition()
                .delay(1300)
                .duration(magDur)
                  .attr('viewBox', '0 0 ' + bounds.width + ' ' + bounds.height)
                .each('end', () => zoom.remove())

              bar
                .style('fill-opacity', '0.6')
                .transition()
                .delay(1300)
                .duration(magDur)
                  .style('fill-opacity', '1')

              d3.transition()
                .delay(300)
                .duration(diffDur)
                  .tween('diff', () => {
                    var i = d3.interpolateRound(0, scope.diff)
                    return function(t) {
                      $timeout(() => scope.delta = i(t))
                    }
                  })
            }


            if (scope.diff > 0) {
              diffVal = scope.diff
              d = x(scope.diff) - x(0)
              svg.selectAll('.diff')
                .attr('x', x(scope.level))
                .attr('width', 0)
                .transition()
                .delay(300)
                .duration(diffDur)
                  .attr('width', d)

              svg.selectAll('.arrow')
                .attr('d', rightArrow(scope.level, scope.level, 11))
                .transition()
                .delay(300)
                .duration(diffDur)
                  .attr('d', rightArrow(scope.level, scope.level + scope.diff, 11))

              svg.selectAll('.text').text('+' + $filter('round1')(scope.diff))
              var w = text.style('width').replace('px','')

              svg.selectAll('.text')
                .attr('text-anchor', 'start')
                .attr('x', x(scope.level + scope.diff))
                .attr('dx', 4)
            } else if (scope.diff < 0) {
              diffVal = scope.diff
              d = x(-scope.diff) - x(0)
              svg.selectAll('.diff')
                .attr('x', x(scope.level))
                .attr('width', 0)
                .style('fill', Aspects.getColor(scope.name, 40))
                .transition()
                .delay(300)
                .duration(diffDur)
                  .attr('x', x(scope.level) - d)
                  .attr('width', d)

              svg.selectAll('.arrow')
                .attr('d', leftArrow(scope.level, scope.level, 11))
                .transition()
                .delay(300)
                .duration(diffDur)
                  .attr('d', leftArrow(scope.level, scope.level + scope.diff, 11))

              svg.selectAll('.text').text($filter('round1')(scope.diff))

              svg.selectAll('.text')
                .attr('text-anchor', 'end')
                .attr('dx', -4)
                .attr('x', x(scope.level + scope.diff))
            } else if (diffVal > 0) {
              d = x(diffVal) - x(0)
              svg.selectAll('.diff')
                .attr('x', x(scope.level))
                .attr('width', d)
                .transition()
                .duration(diffDur)
                  .attr('width', 0)
            } else if (diffVal < 0) {
              d = x(-diffVal) - x(0)
              svg.selectAll('.diff')
                .attr('x', x(scope.level) - d)
                .attr('width', d)
                .style('fill', Aspects.getColor(scope.name, 40))
                .transition()
                .delay(300)
                .duration(diffDur)
                  .attr('x', x(scope.level))
                  .attr('width', 0)
            }
          }
        }
      }
    }
  })
  .directive('fix', function ($window) {
    return {
      restrict: 'EA',
      scope: {},
      link: function(scope, element, attrs) {
        var div = d3.select(element[0])
        var parent = d3.select(div[0][0].parentNode)

        window.onresize = function() {
          scope.$apply()
        }

        scope.$watch(function() {
          return angular.element($window)[0].innerWidth
        }, function() {
          scope.render()
        })

        scope.render = function () {
          var bounds = parent[0][0].getBoundingClientRect()
          div.style({
            'position': 'fixed',
            'padding': parent.style('padding'),
            'top': window.scrollTop ? bounds.top + window.scrollTop + 'px' : bounds.top + 'px',
            'left': window.scrollLeft ? bounds.left + window.scrollLeft +  'px' : bounds.left + 'px',
            'width': bounds.width + 'px'
          })
        }
      }
    }
  })
  .directive('prog', function ($window) {
    return {
      restrict: 'EA',
      scope: {
        complete: '='
      },
      link: function(scope, element, attrs) {
         var svg = d3.select(element[0])
          .append('svg')
          .attr('width', '100%')
          .attr('height', 8)

        window.onresize = function() {
          scope.$apply()
        }

        scope.$watch(function() {
          return angular.element($window)[0].innerWidth
        }, function() {
          scope.render()
        })

        scope.$watch('complete', function () {
          scope.render()
        })

        scope.render = function () {
          svg.selectAll('*').remove()
          var bounds = svg[0][0].getBoundingClientRect()
          svg.append('rect')
            .attr('height', 8)
            .attr('width', bounds.width * scope.complete)
            .attr('fill', '#559')
        }
      }
    }
  })
  .directive('back', function (Aspects) {
    return {
      restrict: 'EA',
      scope: {
        name: '='
      },
      link: function(scope, element, attrs) {
        scope.$watch('name', function (name) {
          element[0].style.border = '1px solid ' + Aspects.getColor(name, 60)
        })
      }
    }
  })