angular.module('well.survey', [])
  .controller('ResultsCtrl', function ($scope, Aspects, Survey, Choices, User) {
    $scope.weights = Choices.weights()
    $scope.aspects.sorted = _.sortBy($scope.aspects.names, function (n) {
      return $scope.weights[n] * -1
    })

    $scope.data = User.getData('threetwo')
    
    // generate some dummy data for testing
    if (!$scope.weights) {
      $scope.weights = {}
      _.each($scope.aspects.names, function (a) {
        $scope.weights[a] = Math.random()
      })
      console.log($scope.weights)

      var sumW = 0
       _.each($scope.aspects.names, function (a) {
        sumW += $scope.weights[a]
      })

      _.each($scope.aspects.names, function (a) {
        $scope.weights[a] = $scope.weights[a] / sumW
      })
    }
  })
  .controller('SurveyCtrl', function ($scope, Aspects, Survey, Style, $location, $routeParams, Trans) {
    Aspects.names().then(aspects => {
      $scope.aspects.names = aspects
      Survey.aspects = aspects
    })

    $scope.survey = {
      finished: false
    }

    $scope.$watch('responses', function () {
      if (Survey.responded()) {
        $scope.survey.finished = true
      }
    }, true)

    $scope.next = function () {
      switch ($location.path().split('/')[1]) {
        case 'threetwo':
          $location.path('threetwo/instructions')
          break
        case 'overlap':
          $location.path('overlap/instructions')
          break
        case 'fineoverlap':
          $location.path('fineoverlap/instructions')
          break
      }
    }
  })
  .factory('Survey', function (Aspects, $q) {
    var survey = this
    this.responses = {}

    this.getResponses = () => {
      var deferred = $q.defer()

      if (this.aspects) {
        deferred.resolve(survey.responses)
      } else {
        Aspects.names().then(aspects => {
          survey.aspects = aspects
          _.each(aspects, function (a) {
            if (!survey.responses[a]) survey.responses[a] = null
          })
          deferred.resolve(survey.responses)
        })
      }
      return deferred.promise
    }

    this.respond = (aspect, level) => this.responses[aspect] = level

    this.responded = () => this.aspects && _.every(survey.aspects, a => survey.responses[a])

    this.randomize = () => {
      console.log('randomized')
      var deferred = $q.defer()
      this.getResponses().then(responses => {
        Aspects.names().then(aspects => {
          for (var i in aspects) {
            survey.responses[aspects[i]] = Math.max( Math.ceil(Math.random() * 99), Math.max( Math.ceil(Math.random() * 99), Math.ceil(Math.random() * 99) ) )
          }

          deferred.resolve(survey.responses)
        })
      })
      return deferred.promise
    }

    this.safeRandomize = () => {
      if (this.responded()) { 
        return this.getResponses()
      } else {
        return this.randomize()
      }
    }

    this.reset = () => {
      if (this.aspects) _.each(survey.aspects, a => {
        survey.responses[a] = null
      })
      return this.responses
    }

    this.minMax = null
    this.range = () => {
      var deferred = $q.defer()
      if (this.minMax) {
        deferred.resolve(this.minMax)
      } else {
        this.safeRandomize().then(responses => {
          var values = _.map(responses, r => r)
          var min = Math.max(0, _.min(values) - 8)
          var max = Math.min(100, _.max(values) + 8)
          this.minMax = [min, max]
          console.log(this.minMax)
          deferred.resolve(this.minMax)
        })
      }
      return deferred.promise
    }

    return this
  })
  .controller('TransCtrl', function ($scope, Aspects, Survey, Trans, $rootScope, $timeout) {
    Aspects.names().then(aspects => {
      $scope.aspects.names = aspects
      Survey.aspects = aspects
    })
    Survey.safeRandomize().then(res => {
      // Trans.ition()
    })

    $scope.resize = false
    $rootScope.$on('trans:change', () => {
      $timeout(() => $scope.resize = !$scope.resize )
    })
  })
  .factory('Trans', function ($timeout, $rootScope, Style) {
    var trans = this

    this.ition = () => {
      setTimeout(() => {
        d3.select('.option .panel').insert('h3', 'li')
        d3.selectAll('range')
          .style('opacity', 1)
          .transition()
          .duration(1000)
            .style('opacity', 0)
            .remove()
          .each('end', () => {
            d3.select('.option h3').text('Option 1')
          })

       d3.selectAll('.axis text')
          .filter(d => d > 0 && d < 100)
          .style('opacity', 1)
          .transition()
          .duration(1000)
            .style('opacity', 0)
          .each('end', () => {
            Style('.axis .tick', 'display:none;')
            Style('.axis .tick:first-child, .axis .tick:last-child', 'display:block;')
          })

        d3.selectAll('range svg')
          .style('height', 20)
          .transition()
          .duration(1000)
            .style('height', 0)

        var width = d3.select('.option').style('width').replace('px','')
        d3.select('.option')
          .style('width', width + 'px')
          .transition()
          .delay(1000)
          .duration(1000)
            .style('width', width / 2 + 'px')
            .tween('event', () => {
              return function(t) {
                $rootScope.$broadcast('trans:change')
              }
            })

      }, 1)
    }
    return trans
  })