angular.module('well.instruct', [])
  .controller('InstructorCtrl', function ($scope, Survey, $location, $timeout) {
    
    Survey.safeRandomize()

    $scope.instructions = {
      
      step: 0,
      
      next: () => $scope.instructions.step++,
      
      prev: () => $scope.instructions.step--,
      
      repeat: () => $scope.instructions.step = $scope.instructions.choice = 0,

      animOne: null,

      animTwo: null,
      
      understood: () => {
        switch ($location.path().split('/')[1]) {
          case 'threetwo':
            $location.path('threetwo/abbcca')
            break
          case 'overlap':
            $location.path('overlap/abac')
            break
          case 'fineoverlap':
            $location.path('fineoverlap/tradeoff')
            break
        }
      }
    
    }

    $scope.$watch('instructions.step', (step) => {
      $scope.instructions.animOne = $scope.instructions.animTwo = null
      if (step === 2) $timeout(() => {
        $scope.instructions.animOne = 'one'
      }, 2000)

      if (step === 3) $timeout(() => {
        $scope.instructions.animTwo = 'one'
      }, 2000)
    })

  })
  .controller('InstructCtrl', function ($scope, Survey, $interval, $timeout, Point, $location) {
    Survey.safeRandomize()

    $scope.instructions = {
      next: () => {
        switch ($location.path().split('/')[1]) {
          case 'threetwo':
            $location.path('threetwo/abbcca')
            break
          case 'overlap':
            $location.path('overlap/abac')
            break
          case 'fineoverlap':
            $location.path('fineoverlap/tradeoff')
            break
        }
      }
    }

    $scope.delay = 6000
    $scope.instructions.partZero = true
    var animate = () => {
      switch ($scope.t / $scope.delay) {
        case 1:
          $scope.instructions.partOne = true
          $scope.instructions.partZero = false
          break

        case 2:
          $scope.instructions.partTwo = true
          $scope.instructions.diffOne = 4
          $scope.instructions.animOne = 'one'
          break

        case 3:
          $scope.instructions.partOne = false
          $scope.instructions.partThree = true
          break

        case 4:
          $scope.instructions.partFour = true
          $scope.instructions.diffTwo = 4
          $scope.instructions.animTwo = 'one'
          break

        case 5:
          $scope.instructions.partThree = false
          $scope.instructions.partFive = true
          $scope.instructions.highlightOne = true
          $scope.instructions.highlightTwo = true
          Point.leftRight()
          break

        case 6:
          $scope.animating = false
          $interval.cancel($scope.instructions.promise)
          $scope.instructions.highlightOne = false
          $scope.instructions.highlightTwo = false
          $scope.instructions.done = true
      }
    }

    $scope.instructions.run = () => {
      Point.hide()
      $scope.instructions.done = false
      $scope.instructions.partZero = true
      $scope.instructions.partOne = false
      $scope.instructions.partTwo = false
      $scope.instructions.partThree = false
      $scope.instructions.partFour = false
      $scope.instructions.partFive = false
      $scope.instructions.highlightOne = false
      $scope.instructions.highlightTwo = false

      $scope.instructions.diffOne = 0
      $scope.instructions.diffTwo = 0

      $scope.t = 0
      $scope.completed = 0
      $scope.animating = true

      $scope.instructions.promise = $interval(() => {
        if ($scope.animating) {
          $scope.t += 10
          $scope.completed = $scope.t / ($scope.delay * 6)
          animate()
        }
      }, 10)
    }

    $scope.instructions.run()

  })
  .directive('stepper', function () {
    return {
      restrict: 'E',
      scope: {
        steps: '=',
        parent: '='
      },
      templateUrl: 'stepper.html',
      link: function (scope, element, attributes) {
        console.log(scope.steps)
      }
    }
  })
  .factory('Point', function ($timeout) {
    var point = this
    var pointer = d3.select('.pointer-hand')

    this.mouse = { x: 400, y: 400 }
    document.onmousemove = (event) => {
      this.mouse.x = event.clientY
      this.mouse.y = event.clientX
    }

    this.isWithin = (element) => {
      var bounds = document.querySelector(element).getBoundingClientRect()
      var pos = document.querySelector('.pointer').getBoundingClientRect()
      return pos.left > bounds.left && pos.left < bounds.left + bounds.width
        && pos.top > bounds.top && pos.top < bounds.top + bounds.height
    }

    this.hide = () => {
      pointer.style('display', 'none')
    }

    this.leftRight = () => {
      var left = document.querySelector('.left').getBoundingClientRect()
      var right = document.querySelector('.right').getBoundingClientRect()
      pointer
        .style('display', 'block')
        .style('left', this.mouse.y  + 'px')
        .style('top', this.mouse.x + 'px')
        .transition()
        .duration(2000)
          .style('left', left.left + (left.width / 2) + 'px')
          .style('top', left.top + (left.height / 2) + 'px')
          .tween('position', () => {
            return function(t) {
              d3.select('.left').classed('hover', point.isWithin('.left'))
              d3.select('.right').classed('hover', point.isWithin('.right'))
            }
          })
          .transition()
          .duration(1000)
            .style('left', right.left + (right.width / 2) + 'px')
            .style('top', right.top + (right.height / 2) + 'px')
            .tween('position', () => {
              return function(t) {
                d3.select('.left').classed('hover', point.isWithin('.left'))
                d3.select('.right').classed('hover', point.isWithin('.right'))
              }
            })
            .each('end', () => {
              d3.select('body').append('svg')
                .attr('width', '100%')
                .attr('height', '100%')
                .style({
                  position: 'fixed',
                  top: 0,
                  left: 0,
                  'z-index': 999,
                  'pointer-events': 'none'
                })
                .append('circle')
                  .attr('cx', right.left + (right.width / 2))
                  .attr('cy', right.top + (right.height / 2))
                  .attr('r', 0)
                  .style('fill', 'none')
                  .style('stroke', '#000')
                  .style('stroke-opacity', 1)
                  .style('stroke-width', '2px')
                  .transition()
                  .duration(1000)
                    .attr('r', 100)
                    .style('stroke-opacity', 0)

              d3.select('.left')
                .style('opacity', 1)
                .transition()
                .duration(1000)
                .style('opacity', 0.3)
            })
    }

    return this
  })